.navbar {
    background-color: #007bff;
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .navbar-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar-logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: white;
    background: none;
    border: none;
    cursor: pointer;
    text-decoration: none;
  }
  
  .navbar-logo:hover {
    color: #ffc107;
  }
  
  .navbar-add-button {
    background-color: #07ff72;
    color: #050a10;
    border: none;
    padding: 8px 15px;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .navbar-add-button:hover {
    background-color: #00e677;
    color: white;
  }
  
  .navbar-button {
    background-color: white;
    color: #007bff;
    border: none;
    padding: 8px 15px;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .navbar-button:hover {
    background-color: #0056b3;
    color: white;
  }
  