/* Global styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f7f7f7;
  }
  
  h1, h2 {
    text-align: center;
    color: #333;
  }
  
  /* Jersey List/Grid */
  .jersey-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .jersey-card {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 15px;
    text-align: center;
    transition: transform 0.2s;
  }
  
  .jersey-card:hover {
    transform: translateY(-5px);
  }
  
  .jersey-card img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .jersey-card h3 {
    margin: 10px 0;
    color: #444;
  }
  
  .jersey-card p {
    margin: 5px 0;
    color: #666;
  }
  
  /* Buttons */
  button {
    margin-top: 10px;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  button.delete {
    background-color: #dc3545;
  }
  
  button.delete:hover {
    background-color: #b71c1c;
  }
  
  /* Filters and Form Container */
  .filters, .form-container {
    margin: 20px auto;
    padding: 15px;
    background: #fff;
    max-width: 600px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .filters select, .filters input {
    margin-right: 10px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .form-container {
    padding: 20px;
    margin: 20px auto;
  }
  
  .form-container h2 {
    text-align: center;
    color: #333;
  }
  
  /* Form fields */
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
  }
  
  .form-control {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    width: 100%;
  }
  
  .form-container select {
    margin-top: 5px;
  }
  
  /* Image preview */
  .image-preview img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  /* Form Buttons */
  .form-container button {
    margin-top: 10px;
    width: 100%;
  }
  
  .btn-primary {
    background-color: #007bff;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
  }
  /* Style pour la liste déroulante */
.form-container select {
    background-color: #fff; /* Couleur de fond lisible */
    color: #333; /* Couleur du texte */
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-top: 5px;
    width: 100%;
    appearance: none; /* Supprime le style natif pour uniformiser */
  }
  
  .form-container select option {
    background-color: #fff; /* Fond blanc pour les options */
    color: #333; /* Texte sombre pour une meilleure visibilité */
  }
  
  /* Ajout d'une bordure et d'une ombre pour le focus */
  .form-container select:focus {
    border-color: #007bff; /* Couleur bleue pour le focus */
    box-shadow: 0 0 4px rgba(0, 123, 255, 0.5); /* Effet lumineux */
    outline: none; /* Supprime le contour par défaut */
  }
  /* Section des filtres */
 /* Conteneur principal de la navbar des filtres */
.filter-navbar {
    display: flex; /* Aligne les filtres horizontalement */
    justify-content: space-around; /* Répartit l'espace uniformément */
    align-items: center; /* Centre les éléments verticalement */
    background-color: #007bff; /* Couleur de fond */
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    color: white;
    width: 100%; /* Prend toute la largeur de la page */
    margin-bottom: 20px; /* Ajoute un espace sous la navbar */
  }
  
  /* Chaque élément de filtre */
  .filter-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 10px; /* Espacement entre les filtres */
    color: white;
  }
  
  /* Label des filtres */
  .filter-item label {
    margin-bottom: 5px;
    font-weight: bold;
    color: white;
    font-size: 0.9rem;
  }
  
  /* Style des select et input */
  .filter-item select,
  .filter-item input {
    width: 150px; /* Taille fixe pour uniformité */
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 0.9rem;
    color: #333;
    transition: border-color 0.3s ease;
  }
  
  /* Hover et focus */
  .filter-item select:focus,
  .filter-item input:focus {
    border-color: #0056b3;
    outline: none;
  }
  
  .filter-item select:hover,
  .filter-item input:hover {
    border-color: #0056b3;
  }
